import {Action} from '@ngrx/store';

// travel curator
export const TRY_FETCH_TRAVELCURATOR = 'TRY_FETCH_TRAVELCURATOR';
export const SET_TRAVELCURATOR = 'SET_TRAVELCURATOR';
export const TRAVELCURATOR_EDIT = 'TRAVELCURATOR_EDIT';
export const UPLOAD_TRAVELCURATOR_IMAGES = 'UPLOAD_TRAVELCURATOR_IMAGES';



// classes for travel curator
export class TryFetchTravelCurator implements Action {
    readonly type = TRY_FETCH_TRAVELCURATOR;
}

export class SetTravelCurator implements Action {
    readonly type = SET_TRAVELCURATOR;

    constructor(public payload: {}) {
    }
}

export class UploadTravelCuratorImages {
    readonly type = UPLOAD_TRAVELCURATOR_IMAGES;

    constructor(public payload: any) {
    }
}

export class TravelCuratorEdit implements Action {
    readonly type = TRAVELCURATOR_EDIT;

    constructor(public payload: {}) {
    }
}




export type TravelcuratorActions =
    TravelCuratorEdit | UploadTravelCuratorImages | SetTravelCurator | TryFetchTravelCurator ;
