import {Component, OnInit, ViewChild} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Store} from '@ngrx/store';
import {TravelcuratorModel} from '../travelcurator.model';
import * as TravelcuratorActions from "../store/travelcurator.actions";
import {TravelCuratorState} from "../store/travelcurator.reducer";

@Component({
  selector: 'app-travelcuratoredit',
  templateUrl: './travelcuratoredit.component.html',
  styleUrls: ['./travelcuratoredit.component.css']
})
export class TravelcuratoreditComponent implements OnInit {

  @ViewChild('t', {static: false}) tabSet;

  editForm: FormGroup;
  ownerForm: FormGroup;
  imageForm: FormGroup;

  registrationMsmeImage: File = null;
  registrationGstImage: File = null;
  registrationCinImage: File = null;

  previewUrlregistrationMsmeNumber: any = '../../../assets/icons/uploadPic.png';
  previewUrlgstNumber: any = '../../../assets/icons/uploadPic.png';
  previewUrlCinProof: any = '../../../assets/icons/uploadPic.png';

  travelCuratorStore: TravelcuratorModel;

  constructor(private fb: FormBuilder, private store: Store<TravelCuratorState>) {

  }

  ngOnInit() {

    this.store.dispatch(new TravelcuratorActions.TryFetchTravelCurator());

    this.imageForm = this.fb.group({
      registrationCinProof: [''],
      gstNumberProof: [''],
      registrationMsmeNumberProof: ['']
    });

    this.ownerForm = this.fb.group({
      ownerDetails: this.fb.group({
        profileRegistrar: ['OWNER', Validators.required],
        name: ['', Validators.required],
        mobile: ['', Validators.required],
        email: ['', Validators.required],
        pan: ['', Validators.required],
        address: this.fb.group({
          address: [''],
          pincode: [''],
          phoneNo: ['']
        })
      }),
      bankDetails: this.fb.group({
        bankName: [''],
        bankAccountNo: [''],
        bankIfsc: [''],
        bankAddress: ['']
      }),
    });


    this.editForm = this.fb.group({

      name: ['', Validators.required],
      registrationType: ['privateLimitied', Validators.required],
      registrationNo: ['', Validators.required],
      officeAddress: ['', Validators.required],
      registrationAuthority: ['', Validators.required],
      registrationMsmeNumber: ['', Validators.required],
      gstNumber: ['', Validators.required],
      panNumber: ['', Validators.required],
      unionEnrollmentNumber: ['', Validators.required],

      firmAddress: this.fb.group({
        permanentAddress: this.fb.group({
          address: [''],
          pincode: [''],
          phoneNo: ['']
        })
      })
    });
    this.store.select('travelCurator').subscribe(
      value => {
        console.log(value);
        this.travelCuratorStore = value.travelCurator;

        this.previewUrlCinProof = value.travelCurator.registrationCinProof;
        this.previewUrlgstNumber = value.travelCurator.gstNumberProof;
        this.previewUrlregistrationMsmeNumber = value.travelCurator.registrationMsmeNumberProof;

        this.editForm.patchValue(this.travelCuratorStore);
        this.imageForm.patchValue(this.travelCuratorStore);
        this.ownerForm.patchValue(this.travelCuratorStore);
      }
    );
  }


  imgPreviewregistrationMsmeNumber(fileInput: any) {
    this.registrationMsmeImage = fileInput.target.files[0] as File;
    const mimeType = this.registrationMsmeImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.registrationMsmeImage);
    reader.onload = () => {
      this.previewUrlregistrationMsmeNumber = reader.result;
    };
  }

  imgPreviewgstNumber(fileInput: any) {
    this.registrationGstImage = fileInput.target.files[0] as File;
    const mimeType = this.registrationGstImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.registrationGstImage);
    reader.onload = () => {
      this.previewUrlgstNumber = reader.result;
    };
  }

  imgPreviewaddress(fileInput: any) {
    this.registrationCinImage = fileInput.target.files[0] as File;
    const mimeType = this.registrationCinImage.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(this.registrationCinImage);
    reader.onload = () => {
      this.previewUrlCinProof = reader.result;
    };
  }

  onSubmit(type: string) {

    if (type === 'imageForm') {
      const data = new FormData();
      data.append('registrationCinProof', this.registrationCinImage);
      data.append('gstNumberProof', this.registrationGstImage);
      data.append('registrationMsmeNumberProof', this.registrationMsmeImage);

      this.store.dispatch(new TravelcuratorActions.UploadTravelCuratorImages(data));
    } else {
      if (type === 'ownerForm') {
        this.tabSet.select('imagesForm');
      } else if (type === 'editForm') {
        this.tabSet.select('ownerDetails');
      }
      Object.assign(this.travelCuratorStore, this.editForm.value);
      Object.assign(this.travelCuratorStore, this.ownerForm.value);
      this.store.dispatch(new TravelcuratorActions.TravelCuratorEdit(this.travelCuratorStore));
    }
  }
}
