import {TravelcuratorActions} from './travelcurator.actions';
import {TravelcuratorModel} from "../travelcurator.model";
import {AppState} from "../../../../store/app.reducer";

export interface TravelCuratorState extends AppState {
    travelCurator: State;
}

export interface State {
    travelCurator: TravelcuratorModel;
}

const initialState: State = {
    travelCurator: new TravelcuratorModel('', '', '', '', '', '', '', '', '', '', {
        permanentAddress: {
            address: '',
            pincode: '',
            phoneNo: ''
        }
    }, {
        profileRegistrar: '', name: '', mobile: '', email: '', pan: '', address: {
            address: '',
            phoneNo: '',
            pincode: ''
        }, bankDetails: {
            bankAccountNo: '',
            bankAddress: '', bankIfsc: '', bankName: ''
        }
    }, '', '', '')
};

export function TravelcuratorReducer(state = initialState, action: TravelcuratorActions) {
    switch (action.type) {
        case 'SET_TRAVELCURATOR': {
            return {
                ...state,
                travelCurator: action.payload
            };
        }
        default: {
            return {
                ...state
            };
        }

    }
}
