import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {TravelcuratorComponent} from "./travelcurator.component";
import {TravelcuratoreditComponent} from "./travelcuratoredit/travelcuratoredit.component";


const routes: Routes = [
  {
    path: '', component: TravelcuratorComponent, children: [
      {path: 'dashboard', component: TravelcuratorComponent},
      {path: 'editProfile', component: TravelcuratoreditComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TravelcuratorRoutingModule { }
