import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {map, switchMap} from 'rxjs/operators';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as endPoints from '../../../../shared/serverEndpoints';
import * as TravelCuratorAction from './travelcurator.actions';

@Injectable()
export class TravelcuratorEffects {
    constructor(public action: Actions, public http: HttpClient) {
    }

// effects for travel curator
    @Effect()
    editTravelCurator = this.action.pipe(
        ofType(TravelCuratorAction.TRAVELCURATOR_EDIT),
        map((payload: TravelCuratorAction.TravelCuratorEdit) => {
            return payload.payload;
        }),
        switchMap((data) => {
            return this.http.post(endPoints.edittravelcurator, data, {observe: 'response'});
        }),
        switchMap((response: HttpResponse<any>) => {
            if (response.status === 200) {
                return [
                    {
                        type: TravelCuratorAction.SET_TRAVELCURATOR,
                        payload: response.body
                    }
                ];
            }
        })
    );


    @Effect()
    uploadTravelCuratorImages = this.action.pipe(
        ofType(TravelCuratorAction.UPLOAD_TRAVELCURATOR_IMAGES),
        map((data: TravelCuratorAction.UploadTravelCuratorImages) => {
            return data.payload;
        }), switchMap((data: { registrationCinImage: File, registrationGstImage: File, registrationMsmeImage: File, registrationRcImage: File }) => {
            return this.http.post(endPoints.uploadtravelcuratorImages, data, {observe: 'response'});
        }),
        switchMap((response: HttpResponse<any>) => {
            if (response.status === 200) {
                return [
                    {
                        type: TravelCuratorAction.SET_TRAVELCURATOR,
                        payload: response.body
                    }
                ];
            }
        })
    );

    @Effect()
    fetchTravelCurator = this.action.pipe(
        ofType(TravelCuratorAction.TRY_FETCH_TRAVELCURATOR),
        switchMap(() => {
            return this.http.get(endPoints.fetchtravelcurator, {observe: 'response'});
        }),
        switchMap((response: HttpResponse<any>) => {
            if (response.status === 200) {
                return [
                    {
                        type: TravelCuratorAction.SET_TRAVELCURATOR,
                        payload: response.body
                    }
                ];
            }
        })
    );


}
